import React from 'react'

import Text from '../../components/Text';
import Container from '../../components/Container';

import PartI from './PartI'
import PartII from './PartII'
import PartIII from './PartIII'

const titles = [
  'I', 'II', 'III'
]

const part = [
  PartI,
  PartII,
  PartIII
]

const Equipment = ({ match: { params: { id } } }) => {
  const title = titles[id - 1]
  const Content = part[id - 1]
  return (
    <Container maxWidth="800px">
      <Text.H1> PART {title}</Text.H1>
      <Text.H6>
         /  / 
      </Text.H6>
      <Content />
      <Text.H6 textAlign="left" mb="2rem"></Text.H6>
      <Text fontWeight="bold">
         Shu Wei Hsu<br />
        <br />
        <br />
        <br />
        (LNT Master course)<br />
        Wilderness First Responder, WMA <br />
      </Text>
    </Container>
  )
}

export default Equipment
